    margin: 0;
    background-image: linear-gradient( 109.6deg, rgba(156,252,248,1) 11.2%, rgba(110,123,251,1) 91.1% );
}

.mainContainer {
    width: 100vw;
    height: 100vh;
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.projectForm {
    position: absolute;
    width: 50%;
    right: 0;
    left: 250px;
    margin-left: auto;
    margin-right: auto;
    top: 140px;
    z-index: 6;
    display: none;
    grid-template: 1fr 1.2fr / repeat(6, 1fr);
    background-color: rgb(144, 198, 198);
    overflow: hidden;
    border: 2px solid;
    border-radius: 5px;
    
}

.projectForm > button {
    height: 60%;
    align-self: center;
}

.project {
    height: 20px;
    background-color: aliceblue;
    margin: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: .85em;
    max-width: 100%;
    white-space: pre-wrap;
    overflow: hidden;
    padding: 5px;
    border: 2px solid;
    border-radius: 5px;
    box-shadow: 2px 2px black;
}

.project > p {
    max-width: 100%;
    text-overflow: ellipsis;
    overflow: hidden;
}

.projectContainer {
    grid-row: 2;
    height: 100%;
    max-width: 100%;
    overflow: hidden;
}

.projectTitle {
    background-color: rgba(167, 209, 194, 0.665);
    border: 2px;
    width: 100%;
    height: 30px;
    grid-column: 1 / -1;
    padding: 0;
    padding-left: 10px;
}

.todoItemContainer {
    width: 70%;
    height: 65%;
    background-color: aliceblue;
    z-index: 0;
    position: absolute;
    display: grid;
    border-radius: 15px;
    border: 2px solid rgb(67, 108, 145);
    grid-template-rows: repeat(10, 1fr);
    grid-template-columns: 1fr 1fr 3fr;
    flex-direction: column;
    align-items: center;
    gap: 10px 0px;
    overflow: hidden;
}

.menu {
    grid-column: 1;
    grid-row: 1 / -1;
    width: 100%;
    height: 100%;
    
    display: grid;
    grid-template-rows: 1fr repeat(6, 2fr);
    grid-template-columns: 1fr;
    z-index: 5;
}

.menuItem {
    border-bottom: 2px solid;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 1.5em;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: aliceblue;
    z-index: 5;
    border-right: 3px solid rgba(0, 0, 0, 0.566);
}

.menuItem > h3,
.menuItem > h4 {
    margin: 0;
    user-select: none;
}

#sort {
    border-top-left-radius: 15px;
}

#notes {
    border-bottom: 0px;
    border-bottom-left-radius: 15px;
}

#sortArrow {
    position: absolute;
    left: 130px;
    font-size: .8em;
    transition: .4s;
}

.arrowRotate {
    rotate: 180deg;
}

.sortMenu {
    grid-column: 1;
    grid-row: 1 / 4;
    position: absolute;
    z-index: 4;
    width: 22%;
    min-width: 120px;
    height: 100%;
    background-color: rgb(196, 196, 196);
    left: 18%;
    border-bottom-right-radius: 10px;
    display: none;
    grid-template: repeat(3, 1fr) / 1fr repeat(2, 2fr) .4fr;
}

.projectMenu{
    grid-column: 2;
    grid-row: 1 / -1;
    width: 20%;
    background-color: #efce57;
    height: 100%;
    position: absolute;
    z-index: 4;
    display: none;
    grid-template: .3fr 8fr 1fr / 1fr;
}

.pMenuClose{
    width: 12px;
    justify-self: end;
    margin-right: 3px;
    align-self: center;
}

.pCloseTran{
    animation-name: pMenuClose;
    animation-iteration-count: 1;
    animation-duration: 2s;
    display: none;
}

.pOpenTran{
    animation-name: pMenuClose;
    animation-iteration-count: 1;
    animation-duration: 1s;
    animation-direction: reverse;
    animation-timing-function: cubic-bezier(0.85, 0.15, 0.495, 0.45);
    display: grid;
}

@keyframes pMenuClose {
    0%{
        display: grid;
        transform: translateX(0px);
    }
    100%{
        display: grid;
        transform: translateX(-300px);
    }
}

.newProjectButton{
    grid-row: 3;
    width: 50%;
    height: 50%;
    border-radius: 5px;
    align-self: center;
    justify-self: center;
    background-color: aliceblue;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: Arial, Helvetica, sans-serif;
    border: 2px solid;
    box-shadow: 2px 2px black;
    user-select: none;
}

.newProjectButton:active{
    transform: translateY(1px) translateX(1px);
    box-shadow: 1px 1px black;
}

#sortDateButton {
    width: 75%;
    height: 75%;
    align-self: center;
    
    grid-column: 2;
    border: 2px solid black;
    border-radius: 7px;
    background-color: #ffdb58;
    box-shadow: 1px 1px black;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 700;
    font-size: .9em;
    user-select: none;
}

#sortDateButton:active {
    transform: scale(.95);
    box-shadow: 0px 0px;
    transform: translateX(1px) translateY(1px);
    background-color: #f7d559;
    box-shadow: inset 1px 1px  #00000096;
}

#sortMenuExit {
    grid-column: 4;
    margin: 0;
    width: 100%;
    text-align: center;
    align-self: start;
    position: relative;
    right: 4px;
    top: 3px;
    background-color: #f7d559;
    border-radius: 100px;
}

#sortMenuExit:active {
    transform: scale(.95);
    box-shadow: 0px 0px;
    background-color: #efce57;
    border: 1px solid;
}

.sortMenuTran {
    display: grid;
    animation-name: sortMenuAni;
    animation-duration: 1s;
    animation-iteration-count: 1;
}

.sortMenuCloseTran {
    display: none;
    animation-name: sortMenuCloseAni;
    animation-duration: 1s;
    animation-iteration-count: 1;
}

@keyframes sortMenuAni {
    0%{
        transform: translateX(-260px);
    }
    100%{
        transform: translateX(0px);
    }
}

@keyframes sortMenuCloseAni {
    0%{
        transform: translateX(0px);
        display: grid;
    }
    100%{
        transform: translateX(-260px);
        display: grid;
    }
}

