    box-shadow: 1px 1px black;
}

#todoMore:active,
#todoClose:active {
    transform: scale(.95);
    box-shadow: 0px 0px;
    transform: translateX(1px) translateY(1px);
    background-color: #296bc875;
    box-shadow: inset 1px 1px  #00000096;
}

#todoMore {
    justify-self: start;
    left: 12px;
}

#todoClose {
