    right: 12px;
}

.button {
    margin-left: 20px;
    margin-top: 20px;
    position: absolute;
    bottom: 5%;
}

.newTodo {
    display: flex;
    width: 50%;
    height: 55%;
    z-index: 2;
}

.todoItemDelete {
