.todoMain > p {
    margin: 0;
    position: relative;
    left: 30px;
}

.todoMain > h3 {
    position: relative;
    left: 30px;
    border-bottom: 2px solid;
}

legend {
    font-family: Arial, Helvetica, sans-serif;
}

.todoInputContainer {
    display: flex;
    height: 100%;
    width: 100%;
    background-color: rgba(244, 252, 252, 0.998);
    flex-direction: column;
    justify-content: space-between;
    z-index: 2;
}

input:not(.radio, .projectTitle) {
    border: 0px solid;
    border-radius: 5px;
    box-shadow: inset rgba(103, 103, 103, 0.09) 0px 3px 8px;
    height: 30px;
    width: 80%;
    padding-left: 10px;
    background-color: rgba(0, 0, 0, 0);
    margin-bottom: 20px;
}

input:focus-visible,
textarea:focus-visible{
    border: 0px solid;
    outline: 0px solid;
}

textarea{
    min-width: 60%;
    min-height: 25%;
    max-width: 60%;
    max-height: 25%;
    border: 0px solid;
    border-radius: 5px;
    box-shadow: inset rgba(103, 103, 103, 0.09) 0px 3px 8px;
    padding-left: 10px;
    padding-top: 10px;
    background-color: rgba(0, 0, 0, 0);
    margin-bottom: 20px;
}

#todoDate {
    width: 40%;
}

.priorityRadioContainer {
    display: grid;
    grid-template: 1fr / repeat(3, 1fr);
    width: 80%;
    border: 0px;
    border-radius: 5px;
    box-shadow: inset rgba(103, 103, 103, 0.09) 0px 3px 8px;
}

.priorityRadioContainer > div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
}

.priorityRadioContainer > legend {
    position: relative;
    bottom: 10px;
}

.radio {
    box-shadow: 0px;
}

.submitButton {
    width: 60px;
    position: relative;
    left: 10px;
}

.previewTran {
    animation-name: previewFlip;
    animation-duration: .35s;
    animation-iteration-count: 1;
}

@keyframes previewFlip {
    0%{
        transform: rotateX(0deg);
    }
    100%{
        transform: rotateX(90deg);
    }
}

.previewTranRev {
    animation-name: previewFlipRev;
    animation-duration: .35s;
    animation-iteration-count: 1;
}

@keyframes previewFlipRev {
    0%{
        transform: rotateX(-90deg);
    }
    100%{
        transform: rotateX(0deg);
    }
}

.mainTran {
    animation-name: mainFlip;
    animation-duration: .7s;
    animation-iteration-count: 1;
    position: absolute;
    height: 95%;
    top: 0px;
    margin-top: 8px;
    grid-row: 1;
    transition: .2s;
}

.mainTranRev {
    animation-name: mainFlipRev;
    animation-duration: .7s;
    animation-iteration-count: 1;
    animation-direction: reverse;
    display: none;
}

@keyframes mainFlip {
    0%{
        transform: rotateX(-90deg);
        position: relative;
        grid-row: initial;
        
    }
    50%{
        transform: rotateX(0deg);
        margin-top: calc(var(--margin-amount));
        position: relative;
    }
    50.01%{
        
        position: absolute;
        height: 55px;
        grid-row: initial;
        margin-top: 8px;
    }
    75%{
        
        margin-top: 8px;
    }
    100%{
        height: 95%;
        grid-row: initial;
        display: flex;
    }
}
@keyframes mainFlipRev {
    0%{
        transform: rotateX(90deg);
        
        
        height: 100%;
        visibility: hidden;
    }
    5%{
        grid-row: initial;
        visibility: hidden;
        display: none;
    }
    6%{
        visibility: visible;
    }
    50%{
        transform: rotateX(0deg);
        margin-top: calc(var(--margin-amount));
        position: relative;
    }
    50.0000001%{
        
        position: absolute;
        height: 30px;
        grid-row: initial;
        margin-top: 8px;
        
    }
    100%{
        height: 95%;
        grid-row: initial;
        position: absolute;
        margin-top: 8px;
    }
