 / repeat(6, 1fr);\n    background-color: rgb(144, 198, 198);\n    overflow: hidden;\n    border: 2px solid;\n    border-radius: 5px;\n    \n}\n\n.projectForm > button {\n    height: 60%;\n    align-self: center;\n}\n\n.project {\n    height: 20px;\n    background-color: aliceblue;\n    margin: 10px;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    font-size: .85em;\n    max-width: 100%;\n    white-space: pre-wrap;\n    overflow: hidden;\n    padding: 5px;\n    border: 2px solid;\n    border-radius: 5px;\n    box-shadow: 2px 2px black;\n}\n\n.project > p {\n    max-width: 100%;\n    text-overflow: ellipsis;\n    overflow: hidden;\n}\n\n.projectContainer {\n    grid-row: 2;\n    height: 100%;\n    max-width: 100%;\n    overflow: hidden;\n}\n\n.projectTitle {\n    background-color: rgba(167, 209, 194, 0.665);\n    border: 2px;\n    width: 100%;\n    height: 30px;\n    grid-column: 1 / -1;\n    padding: 0;\n    padding-left: 10px;\n}\n\n.todoItemContainer {\n    width: 70%;\n    height: 65%;\n    background-color: aliceblue;\n    z-index: 0;\n    position: absolute;\n    display: grid;\n    border-radius: 15px;\n    border: 2px solid rgb(67, 108, 145);\n    grid-template-rows: repeat(10, 1fr);\n    grid-template-columns: 1fr 1fr 3fr;\n    flex-direction: column;\n    align-items: center;\n    gap: 10px 0px;\n    overflow: hidden;\n}\n\n.menu {\n    grid-column: 1;\n    grid-row: 1 / -1;\n    width: 100%;\n    height: 100%;\n    \n    display: grid;\n    grid-template-rows: 1fr repeat(6, 2fr);\n    grid-template-columns: 1fr;\n    z-index: 5;\n}\n\n.menuItem {\n    border-bottom: 2px solid;\n    font-family: Arial, Helvetica, sans-serif;\n    font-size: 1.5em;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    background-color: aliceblue;\n    z-index: 5;\n    border-right: 3px solid rgba(0, 0, 0, 0.566);\n}\n\n.menuItem > h3,\n.menuItem > h4 {\n    margin: 0;\n    user-select: none;\n}\n\n#sort {\n    border-top-left-radius: 15px;\n}\n\n#notes {\n    border-bottom: 0px;\n    border-bottom-left-radius: 15px;\n}\n\n#sortArrow {\n    position: absolute;\n    left: 130px;\n    font-size: .8em;\n    transition: .4s;\n}\n\n.arrowRotate {\n    rotate: 180deg;\n}\n\n.sortMenu {\n    grid-column: 1;\n    grid-row: 1 / 4;\n    position: absolute;\n    z-index: 4;\n    width: 22%;\n    min-width: 120px;\n    height: 100%;\n    background-color: rgb(196, 196, 196);\n    left: 18%;\n    border-bottom-right-radius: 10px;\n    display: none;\n    grid-template: repeat(3, 1fr) / 1fr repeat(2, 2fr) .4fr;\n}\n\n.projectMenu{\n    grid-column: 2;\n    grid-row: 1 / -1;\n    width: 20%;\n    background-color: #efce57;\n    height: 100%;\n    position: absolute;\n    z-index: 4;\n    display: none;\n    grid-template: .3fr 8fr 1fr / 1fr;\n}\n\n.pMenuClose{\n    width: 12px;\n    justify-self: end;\n    margin-right: 3px;\n    align-self: center;\n}\n\n.pCloseTran{\n    animation-name: pMenuClose;\n    animation-iteration-count: 1;\n    animation-duration: 2s;\n    display: none;\n}\n\n.pOpenTran{\n    animation-name: pMenuClose;\n    animation-iteration-count: 1;\n    animation-duration: 1s;\n    animation-direction: reverse;\n    animation-timing-function: cubic-bezier(0.85, 0.15, 0.495, 0.45);\n    display: grid;\n}\n\n@keyframes pMenuClose {\n    0%{\n        display: grid;\n        transform: translateX(0px);\n    }\n    100%{\n        display: grid;\n        transform: translateX(-300px);\n    }\n}\n\n.newProjectButton{\n    grid-row: 3;\n    width: 50%;\n    height: 50%;\n    border-radius: 5px;\n    align-self: center;\n    justify-self: center;\n    background-color: aliceblue;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    font-family: Arial, Helvetica, sans-serif;\n    border: 2px solid;\n    box-shadow: 2px 2px black;\n    user-select: none;\n}\n\n.newProjectButton:active{\n    transform: translateY(1px) translateX(1px);\n    box-shadow: 1px 1px black;\n}\n\n#sortDateButton {\n    width: 75%;\n    height: 75%;\n    align-self: center;\n    \n    grid-column: 2;\n    border: 2px solid black;\n    border-radius: 7px;\n    background-color: #ffdb58;\n    box-shadow: 1px 1px black;\n    font-family: Arial, Helvetica, sans-serif;\n    font-weight: 700;\n    font-size: .9em;\n    user-select: none;\n}\n\n#sortDateButton:active {\n    transform: scale(.95);\n    box-shadow: 0px 0px;\n    transform: translateX(1px) translateY(1px);\n    background-color: #f7d559;\n    box-shadow: inset 1px 1px  #00000096;\n}\n\n#sortMenuExit {\n    grid-column: 4;\n    margin: 0;\n    width: 100%;\n    text-align: center;\n    align-self: start;\n    position: relative;\n    right: 4px;\n    top: 3px;\n    background-color: #f7d559;\n    border-radius: 100px;\n}\n\n#sortMenuExit:active {\n    transform: scale(.95);\n    box-shadow: 0px 0px;\n    background-color: #efce57;\n    border: 1px solid;\n}\n\n.sortMenuTran {\n    display: grid;\n    animation-name: sortMenuAni;\n    animation-duration: 1s;\n    animation-iteration-count: 1;\n}\n\n.sortMenuCloseTran {\n    display: none;\n    animation-name: sortMenuCloseAni;\n    animation-duration: 1s;\n    animation-iteration-count: 1;\n}\n\n@keyframes sortMenuAni {\n    0%{\n        transform: translateX(-260px);\n    }\n    100%{\n        transform: translateX(0px);\n    }\n}\n\n@keyframes sortMenuCloseAni {\n    0%{\n        transform: translateX(0px);\n        display: grid;\n    }\n    100%{\n        transform: translateX(-260px);\n        display: grid;\n    }\n}\n\n.todoPreview {\n    width: 90%;\n    height: 100%;\n    max-height: 1fr;\n    background-color: rgb(255, 255, 255);\n    border: 2px solid;\n    border-radius: 10px;\n    box-shadow: 2px 2px black;\n    display: grid;\n    grid-template-columns: 2fr 2fr 1fr 1fr;\n    margin-top: 20px;\n    z-index: 1;\n    font-family: Arial, Helvetica, sans-serif;\n    justify-self: center;\n    min-width: 0;\n    overflow: hidden;\n 